import React from 'react';
import ReactDOM from 'react-dom';
import events from "./eventData.json"

let rows = events.map(function(u){
    return <tr key={u.name}><td><button>Delete</button></td><td>{u.name}</td><td>{u.date}</td><td>{u.location}</td></tr>;
  });


export default class AdminActivity extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            name: '', 
            date: '',
            events: [rows]
        }
        this.addEvent = this.addEvent.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }
    handleChange(event){
        const target = event.target;
        this.setState({[target.name]: event.target.value});
    }
    addEvent(){
        let e = {name: this.state.name, date: this.state.date};
        this.props.addEvent(e);
    }
    addEvent(e){
        this.setState({events: this.state.events.concat(e)});
    }
    render(){
        let events = this.state.events;
        console.log(events);
        if(events){
            for (var i=0; i<events.length; i++){
                rows.concat(<tr><td>{events[i].name}</td><td>{events[i].date}</td></tr>) 
            }
        }
        console.log("rows"+rows)
        return(
        <main>
            <h1 className="title">Activity Management</h1>
            <p className="subHead">Add Activity</p>
            <form id="add">
                <label for="name">Name</label>
                <input type="text" id="name" name="name" value = {this.state.value} onChange ={this.handleChange}></input>
                <label for="date">Date(s)</label>
                <input type="text" id="date" name="date" value = {this.state.value} onChange ={this.handleChange}></input>
                <button type="button" onClick={this.addEvent.bind(this)}>Add</button>
            </form>

            <h1>Upcoming Tournament/Activity Schedule</h1>

           
                <table className="myTable">
                    <thead><tr><th></th><th>Name</th><th>Date</th><th>Location</th></tr></thead>
                    <tbody>
                        {rows}
                    </tbody>
                </table>
        </main>);
    }
}