import React from "react";
import ReactDOM from "react-dom";
import events from "./eventData.json"
import Menu from "./menu"
import Home from "./home"
import Activities from "./activities";
import Login from "./login";
import Application from "./application";
import AdminActivity from "./AdminActivity";


class App extends React.Component {
    constructor(props) {
        super(props);
      
        this.state = { role: "admin", show: "mActivities" }; 

    }
    navHome(event){
        event.preventDefault();
        this.setState({show: "home"});
        console.log("home");
    }
    navTournaments(event){
        event.preventDefault();
        this.setState({show: "activities"});
    }
    render() {
        let content = "";
       
        if (this.state.show == "home"){
            content = <Home />;
        }
        else if(this.state.show == "application"){
            content = <Application />;
        }
        else if(this.state.show == "login"){
            content = <Login />;
        }
        else if(this.state.show == "mActivities" && this.state.role == "admin"){
            content = <AdminActivity />;
        }
        else if(this.state.show == "activities"){
            content = <Activities />;
        }
       
        else{
            content = <Home />;
        }
        
        // statements/logic to set the content variable based on state
        return (
            <>
                <Menu/>
                {content}
            </>
        );
    }
}
// Now rendering the App component!
ReactDOM.render(<App />, document.getElementById("root"));

