import React from 'react';
import ReactDOM from 'react-dom';

// Function Component Definition
export default class menu extends React.Component{
    constructor(props) {
        super(props);
     
        this.state = { role: "user", show: "home" }; 

        this.navHome = this.navHome.bind(this);
        this.navTournaments = this.navTournaments.bind(this);
    }
    navHome(event){
        event.preventDefault();
        this.setState({show: "home"});
        console.log("home");
    }
    navTournaments(event){
        event.preventDefault();
        this.setState({show: "activities"});
    }

    render(){
        if (this.state.role == "user"){
            if (this.state.show == "home"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a className="highlight" onClick={this.navHome}>Home</a></li>
                            <li><a onClick={this.navTournaments}>Tournaments</a></li> 
                            <li><a >Log Out</a></li>   
                        </ul>
                    </nav>
                    </header>);
            }
            else if (this.state.show == "activities"){

            
            return(
                <header>
                <nav>
                    <ul>
                        <li ><a onClick={this.navHome} >Home</a></li>
                        <li><a className="highlight"onClick={this.navTournaments} >Tournaments</a></li> 
                        <li><a >Log Out</a></li>   
                    </ul>
                </nav>
                </header>);
        }
    }
        else if (this.state.role == "guest"){
            if (this.state.show == "home"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a className="highlight" onClick={this.navHome} >Home</a></li>
                            <li><a onClick={this.navTournaments} >Tournaments</a></li> 
                            <li><a >Login</a></li>   
                            <li><a >Apply Here</a></li>  

                        </ul>
                    </nav>
                    </header>);
            }
            else if (this.state.show == "activities"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a onClick={this.navHome}>Home</a></li>
                            <li><a className="highlight" onClick={this.navTournaments}>Tournaments</a></li> 
                            <li><a >Login</a></li>   
                            <li><a >Apply Here</a></li>  
                        </ul>
                    </nav>
                    </header>);
            }
            else if (this.state.show == "application"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a onClick={this.navHome}>Home</a></li>
                            <li><a onClick={this.navTournaments}>Tournaments</a></li> 
                            <li><a >Login</a></li>   
                            <li><a className="highlight">Apply Here</a></li>  
                        </ul>
                    </nav>
                    </header>);
            }
            else if (this.state.show == "login"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a onClick={this.navHome}>Home</a></li>
                            <li><a onClick={this.navTournaments}>Tournaments</a></li> 
                            <li><a className="highlight">Login</a></li>   
                            <li><a >Apply Here</a></li>  
                        </ul>
                    </nav>
                    </header>);
            }
        }
        else if (this.state.role == "admin"){
            if (this.state.show == "home"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a className="highlight" >Home</a></li>
                            <li><a onClick={this.navTournaments} >Tournaments</a></li> 
                            <li><a >Manage Activities</a></li>  
                            <li><a >Logout</a></li>   
                            

                        </ul>
                    </nav>
                    </header>);
            }
            else if (this.state.show == "activities"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a onClick={this.navHome}>Home</a></li>
                            <li><a className="highlight" onClick={this.navTournaments}>Tournaments</a></li> 
                            <li><a >Manage Activities</a></li>
                            <li><a >Logout</a></li>   
                             
                        </ul>
                    </nav>
                    </header>);
            }
            else if (this.state.show == "mActivities"){
                return(
                    <header>
                    <nav>
                        <ul>
                            <li ><a onClick={this.navHome}>Home</a></li>
                            <li><a onClick={this.navTournaments}>Tournaments</a></li> 
                            <li><a className="highlight" >Manage Activities</a></li>
                            <li><a >Login</a></li>   
                             
                        </ul>
                    </nav>
                    </header>);
            }
        }
    
}
}