import React from 'react';
import ReactDOM from 'react-dom';



export default class application extends React.Component{
    constructor(props) {
        super(props);
     
        this.state = { name: "", email: "" , password: "", skill:"Beginner(Never been to a tournament before)", message:"" }; 
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.closeMsg = this.closeMsg.bind(this);

    }
    handleChange(event){
        const target = event.target;
        this.setState({[target.name]: event.target.value});
    }
    handleSubmit(event){
        var form = document.getElementById("application");
        var text = document.getElementById("text");
        var thankYou = document.getElementById("dialog");

        thankYou.classList.toggle("hide");
        thankYou.classList.add('show');
        
    }
    closeMsg(event){
        console.log("check2");
        var thankYou = document.getElementById("dialog");
        var cover = document.getElementById("cover");
        thankYou.classList.toggle("hide");
        thankYou.classList.toggle("show");

        cover.classList.toggle('obscure');

    }
    render(){
    return(
    <main>
            <h1 className="title">Apply Here!</h1>
            <form id="application">
                <label for="name">Name:</label>
                <input type="text" id="name" name="name" value = {this.state.value} onChange ={this.handleChange} required minlength="4" maxlength="20" size="22"></input>

                <label for="email">Email Address:</label>
                <input type="text" id="email" pattern=".+[a-z]{2,4}" name="email" value = {this.state.value} onChange ={this.handleChange} required minlength="10" maxlength="24" size="25"></input>

                <label for="pass">Password:</label>
                <input type="password" id="pass" name="password" value = {this.state.value} onChange ={this.handleChange} required minlength="4" maxlength="18" size="20"></input>

                <label for="skill">Skill Level:</label>
                    <select name="skill" id="skill"> value = {this.state.value} onChange ={this.handleChange}
                        <option value="Beginner">Beginner(Never been to a tournament)</option>
                        <option value="Novice">I've been to a tournament before</option>
                        <option value="Intermediate">I've been to many tournaments</option>
                        <option value="Expert">I'm a contender to win local tournaments</option>
                        <option value="Master">I'm a Top 100 player</option>
                    </select>
                <label>Main Character(s)</label>
                <textarea name="message" value = {this.state.value} onChange ={this.handleChange} rows="4" cols="35">Put your main character here!
                </textarea>
                <button  onClick={this.handleSubmit}>Submit</button>
            </form>
       
            <section id="dialog" className = "hide">
                <h3>Thank you for your Application!</h3>
                <p id="text"></p>
                <button type="button" onClick={this.closeMsg}>Close</button>
            </section>
        
        </main>);
        }
        }